<h2>属性</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>属性名</th>
<th>类型</th>
<th>默认值</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>modelMatrix</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code> 指定 4x4 变换矩阵，将每个点从模型转换为世界坐标。</td>
</tr>
<tr>
<td>debugShowBoundingVolume</td>
<td>Boolean</td>
<td><code v-pre>false</code></td>
<td><code v-pre>optional</code> 指定是否显示此图元的 BoundingVolume， 仅调试使用。</td>
</tr>
<tr>
<td>polylines</td>
<td>Array</td>
<td><code v-pre>[]</code></td>
<td><code v-pre>optional</code> 指定线集合数组。 数组对象结构为<a href="./#/zh/primitive/vc-primitive-polyline"><code v-pre>vc-primitive-polyline</code></a>组件属性。</td>
</tr>
</tbody>
</table>
<hr>
<ul>
<li>参考官方文档：<strong><a href="https://cesium.com/docs/cesiumjs-ref-doc/PolylineCollection.html">PolylineCollection</a></strong></li>
</ul>
<h2>事件</h2>
<table>
<thead>
<tr>
<th>事件名</th>
<th>参数</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer, cesiumObject}</td>
<td>该组件渲染完毕时触发，返回 Cesium 类, viewer 实例，以及当前组件的 cesiumObject。</td>
</tr>
<tr>
<td>mousedown</td>
<td>{button,surfacePosition,target,type,windowPosition}</td>
<td>鼠标在该图元集合上按下时触发。</td>
</tr>
<tr>
<td>mouseup</td>
<td>{button,surfacePosition,target,type,windowPosition}</td>
<td>鼠标在该图元集合上弹起时触发。</td>
</tr>
<tr>
<td>click</td>
<td>{button,surfacePosition,target,type,windowPosition}</td>
<td>鼠标单击该图元集合时触发。</td>
</tr>
<tr>
<td>dblclick</td>
<td>{button,surfacePosition,target,type,windowPosition}</td>
<td>鼠标左键双击该图元集合时触发。</td>
</tr>
<tr>
<td>mousemove</td>
<td>{button,surfacePosition,target,type,windowPosition}</td>
<td>鼠标移动到该图元集合时触发。</td>
</tr>
</tbody>
</table>
<hr>
</section></template>

<script>
    export default {
      data() {
        return {
          width: 5,
          polylines: [],
          polyline1: {},
          positions1: [
            { lng: 90, lat: 20, height: 10000 },
            { lng: 120, lat: 20, height: 10000 }
          ],
          material1: undefined,
          polyline2: {},
          positions2: [
            { lng: 90, lat: 30, height: 10000 },
            { lng: 120, lat: 30, height: 10000 }
          ],
          material2: undefined,
          polyline3: {},
          positions3: [
            { lng: 90, lat: 40, height: 10000 },
            { lng: 120, lat: 40, height: 10000 }
          ],
          material3: undefined,
          polylines: []
        }
      },
      methods: {
        ready(cesiumInstance) {
          const { Cesium, viewer } = cesiumInstance
          const polylines = []
          for (var i = 0; i < 1000; i++) {
            let polyline = {}
            let positions = []
            positions.push({ lng: Math.random() * 40 + 85, lat: Math.random() * 30 + 21 })
            positions.push({ lng: Math.random() * 40 + 85, lat: Math.random() * 30 + 21 })
            polyline.positions = positions
            polylines.push(polyline)
          }
          this.polylines = polylines
          this.material1 = new Cesium.Material({
            fabric: {
              type: 'Color',
              uniforms: {
                color: Cesium.Color.RED
              }
            }
          })
          this.material2 = new Cesium.Material({
            fabric: {
              type: 'PolylineGlow',
              uniforms: {
                color: Cesium.Color.BLUE
              }
            }
          })
          this.material3 = new Cesium.Material({
            fabric: {
              type: 'PolylineArrow',
              uniforms: {
                color: Cesium.Color.PURPLE
              }
            }
          })
        }
      }
    }
  </script>